
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Open Sans', sans-serif;
  background-color: #f9f9f9;
}

.appBlock {
  display: -ms-flexbox;
  display: flex;
}

.mainBlock {
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 30px;
}

.itemsBlock {
  height: 400px;
  overflow-y: auto;
  /* height: max-content; */
  /* min-height: 400px;
  max-height: 400px; */
  width: 100%;
  background-color: white;
  margin: 0 10px;
  max-width: 500px;
  padding: 20px 15px 20px 25px;
  -webkit-box-shadow: 0px 0px 5px gray;
          box-shadow: 0px 0px 5px gray;
}

.itemsBlock h2 {
  font-weight: 300;
  font-size: 36px;
  color: #565a69;
}

.itemsForm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 20px;
}

.commentsBlock {
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  width: 100%;
  margin: 0 10px;
  max-width: 500px;
  padding: 20px 18px 20px 18px;
  -webkit-box-shadow: 0px 0px 5px gray;
          box-shadow: 0px 0px 5px gray;
  background: white;
}

.errorMsg {
  color: red;
  font-size: 14px;
}

.textareaBlock {
  display: -ms-flexbox;
  display: flex;
  padding-top: 15px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.textarea {
  height: 70px;
  -ms-flex: 1 1;
      flex: 1 1;
  padding-left: 15px;
}

.textarea textarea {
  width: 100%;
  height: 70px;
  resize: none;
  padding: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ccc;
}

.textareaAvatar {
  width: 50px;
  height: 50px;
  background-color: #e6e6e6;
}

